@import "../../styles/_colors.scss";
@import "../../styles/global.scss";

.work {
  background-color: $lightestGrey;
  display: flex;
  flex-direction: column;
  align-items: center;

  .workTitle {
    overflow: hidden;
  }

  a {
    text-decoration: none;
    color: $darkestPlum;

    &:hover {
      color: $mediumPlum;
    }
  }

  .row {
    margin: 0 -0.31rem;
    display: flex;
    align-items: center;
    justify-content: center;
    flex-wrap: wrap;


    &.active {
      content: "";
      display: table;
      clear: both;
    }

    .column {
      float: left;
      width: 25%;
      padding: 0 0.63rem;

      @include mobile {
        width: 100%;
        display: block;
      }

      .workCard {
        min-height: 15rem;

        @include mobile {
          min-height: auto;
        }

        .title {
          text-align: center;
        }

      }
    }
  }

  .hide-mobile {
    @include mobile {
      display: none;
    }
  }

  .extra {

    @include mobile {
      font-size: 0.75rem;
      padding: 0.5rem;
    }

  }

}
